




























import App from '../../../../App.vue'
import Api from '../../../../api.js'
export default {
	data() {
		return {
			identity:"游客",
			avatarUrl:'',
			name:'',
			shopName:'无'
		};
	},
	methods:{
		jump(url){
			if (!url) return;
			uni.navigateTo({
				url
			});
		},
		toQRCode: function() {
			var that = this
			if (App.getToken()) {
				uni.navigateTo({
					url: 'QRCodeCard/QRCodeCard'
				})
			} else {
				uni.showToast({
					title: '未登录',
					icon: 'none'
				})
			}
		}
	},
	onShow() {
		var that = this;
		if (App.getToken()) {
			console.log(App.getcompany())
			if(App.getcompany()){
				this.shopName=App.getcompany().company_name;
				this.identity='店员'
			}
			console.log(App.getcompany().company_name);
			uni.request({
				url: Api.seeUser(),
				header: {
					token: App.getToken(),
				},
				data: {
					otherId: App.getUserId()
				},
				success(res) {
					console.log(res)
					if (res.data.code) {
						uni.showToast({
							title: 'token失效！',
							icon: 'none'
						})
						uni.clearStorage()
					} else {
						that.avatarUrl = res.data.user.author_image
						that.name = res.data.user.author_name
					}
				}
			})
		} else {
			uni.showToast({
				title: '未登录',
				icon: 'none'
			})
			setTimeout(function() {
				uni.navigateTo({
					url: '../login'
				})
			}, 1000)
		}
	}
}
